[class*=pf-c-],
[class*=pf-c-]::before,
[class*=pf-c-]::after {
  padding: 0;
  margin: 0;
  background-color: transparent;
}

html {
  font-size: var(--pf-global--root--FontSize, unset) !important;
}

.pf-screen-reader {
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.pf-screen-reader.pf-m-full-size {
  width: 100%;
  height: 100%;
}

.pf-screen-reader.pf-m-absolute {
  position: absolute;
}

.pf-m-tabular-nums {
  font-variant-numeric: tabular-nums;
}

html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

html,
body {
  height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: var(--pf-global--FontWeight--normal);
}

ul {
  list-style: none;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: var(--pf-global--LineHeight--md);
  color: var(--pf-global--Color--100);
}

img,
embed,
iframe,
object,
audio,
video {
  max-width: 100%;
  height: auto;
}

iframe {
  border: 0;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

td,
th {
  padding: 0;
  text-align: left;
}

code,
pre {
  font-family: var(--pf-global--FontFamily--monospace);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

